import * as React from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

function createData(PNR_No, Train_name, Ticket_id, Coach, Seat_no, Date_Journey ) {
  return { PNR_No, Train_name, Ticket_id, Coach, Seat_no, Date_Journey };
}


export default function TicketTable({ Trains, Tickets }) {
    // TO DO -> Get the Details of the Ticket using the Email_Id of user which is either kept in redux datastore or passed using a prop
    const ticket = createData(123, 'Rajdhani', 1, 'Sleeper-2', 22, '23/10/2022' );
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 700 }} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell align="center">PNR Number</StyledTableCell>
            <StyledTableCell align="center">Train Name</StyledTableCell>
            <StyledTableCell align="center">Ticket Number</StyledTableCell>
            <StyledTableCell align="center">Coach</StyledTableCell>
            <StyledTableCell align="center">Seat Number</StyledTableCell>
            <StyledTableCell align="center">Date of Journey</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>           
            <StyledTableRow key={ticket.Ticket_id}>
                <StyledTableCell align="center">{ticket.PNR_No}</StyledTableCell>
                <StyledTableCell align="center">{ticket.Train_name}</StyledTableCell>
                <StyledTableCell align="center">{ticket.Ticket_id}</StyledTableCell>
                <StyledTableCell align="center">{ticket.Coach}</StyledTableCell>
                <StyledTableCell align="center">{ticket.Seat_no}</StyledTableCell>
                <StyledTableCell align="center">{ticket.Date_Journey}</StyledTableCell>                     
            </StyledTableRow>          
        </TableBody>
      </Table>
    </TableContainer>
  );
}
