// Fix the REGISTRATION case
const mainReducer = (state, action) => {
    const { type, payload } = action;
    switch(type){
        case 'REG': {
            let { isAuth, userEmail } = state;
            isAuth = true;
            userEmail = payload.email;
            return {
                isAuth,
                userEmail
            }
        }
        case 'LOG_IN': {
            let { isAuth, userEmail } = state;
            isAuth = true;
            userEmail = payload.email;
            return {
                isAuth,
                userEmail
            }
        }
        case 'LOG_OUT': {
            let { isAuth, userEmail } = state;
            isAuth = false;
            userEmail = '';
            return {
                isAuth,
                userEmail
            }
        }
        default: return state;
    }
}

export default mainReducer;